import React from 'react';

interface TitleProps {
  text: string;
  lineColor: string;
  position?: 'left' | 'right';
}

const Title: React.FC<TitleProps> = ({
  text,
  lineColor,
  position = 'left',
}) => {
  return (
    <div
      className={`flex items-center ${
        position === 'right' ? 'flex-row-reverse' : ''
      }`}
    >
      {/* Title Text */}
      <h2 className="w-[426px] h-[39.99px] text-[36px] font-semibold leading-[110%] text-left">
        {text}
      </h2>

      {/* Spacing Between Text and Line */}
      <div
        className="ml-[10px] w-[877px] h-[39.99px] rounded-[20px]"
        style={{ backgroundColor: lineColor }}
      ></div>
    </div>
  );
};

export default Title;
