import React from 'react';
import Image from 'next/image';

interface IconTextItemProps {
  svgPath: string; // Path to the SVG
  text: string;
  textColor: string;
}

interface IconTextRowProps {
  items: IconTextItemProps[];
}

const IconTextRow: React.FC<IconTextRowProps> = ({ items }) => {
  return (
    <div className="w-[839px] h-[56px] flex justify-between items-center gap-[56px]">
      {items.map((item, index) => (
        <div
          key={index}
          className="flex items-center"
          style={{ width: '254px', height: '56px' }}
        >
          {/* SVG Image */}
          <div className="w-[56px] h-[56px] flex items-center justify-center">
            <Image
              src={item.svgPath}
              alt={item.text}
              width={56}
              height={56}
              className="object-contain"
            />
          </div>

          {/* Text */}
          <span
            className="ml-4 text-[36px] font-semibold leading-[110%] text-left w-[174px] h-[40px]"
            style={{ color: item.textColor }}
          >
            {item.text}
          </span>
        </div>
      ))}
    </div>
  );
};

export default IconTextRow;
